import { updateLoanDetail } from "actions/LoanDetailActions";
import chatIcon from "assets/chatSvg/chatIcon.svg";
import AclGuard from "components/AclGuard/AclGuard";
import AuthValidator from "components/AuthValidator/AuthValidator";
import ButtonBox from "components/ButtonBox/ButtonBox";
import MainMessageBody from "components/ChatComponents/MainMessageBody";
import DialogBox from "components/Dialog/Dialog";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import Loader from "components/Loader/Loader";
import SimpleOverlayPanel from "components/SimpleOverlayPanel/SimpleOverlayPanel";
import Text from "components/Text/Text";
import TextButton from "components/TextButton/TextButton";
import ToastCustom from "components/Toast";
import { ToastPositionType } from "primereact/toast";
import { Suspense, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, Outlet, Route, Routes, useParams } from "react-router-dom";
import { linkAuthRoute, linkBasePath } from "routes";
import ACLService from "services/ACLService";
import ChatServices from "services/ChatServices";
import TechnicalService from "services/transaction/DDE/TechnicalService";
import LoanService from "services/transaction/LoanService";
import {
  formatDate,
  getEnvVariableValue,
  getLocalStorage,
  parseStandardAPIErrorMessage,
  validatePutAPIResponse,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  LOAN_STAGE_STATUS,
  LOCAL_STORAGE,
  SAHAYYA_USER_TYPE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import {
  LINK_DDE_STAGE_AND_STATUS,
  LINK_LOAN_DETAIL,
  LINK_PARTNER_TASK_LIST,
  LINK_TECHNICAL,
  LINK_TECHNICAL_DEVIATION,
  LINK_TECHNICAL_DOCUMENT_EXCHANGE,
  LINK_TECHNICAL_REPORT,
  technicalRoutes,
} from "../../../transactionRoutes";

interface IButtonDisabled {
  stageComplete: boolean;
  submit: boolean;
}

const TECHNICAL_MENU = [
  {
    id: 1,
    name: "Report",
    path: LINK_TECHNICAL_REPORT,
  },
  {
    id: 2,
    name: "Document Exchange",
    path: LINK_TECHNICAL_DOCUMENT_EXCHANGE,
  },
  {
    id: 3,
    name: "Deviations",
    path: LINK_TECHNICAL_DEVIATION,
  },
];

const initialButtonDisabled: IButtonDisabled = {
  stageComplete: false,
  submit: false,
};

const PageTechnical = () => {
  const params = useParams();
  const dispatch = useDispatch();

  const [buttonDisabled, setButtonDisabled] = useState({
    ...initialButtonDisabled,
  });

  const [errorMessage, setErrorMessage] = useState<any>("");
  const [position] = useState<ToastPositionType | undefined>("bottom-right"); //NOSONAR
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [isMessageDialogVisible, setIsMessageDialogVisible] = useState(false);
  const [isNewMessageAvailable, setIsNewMessageAvailable] = useState(false);
  const toastMessageRef = useRef(null);

  const userProfile = useSelector((state: any) => state.UserProfileReducers.profileObj);
  const isPartnerUser = userProfile?.sahayyaUserType === SAHAYYA_USER_TYPE.PARTNER_USER;

  useEffect(() => {
    getLoanDetail();
  }, []);
  useEffect(() => {
    console.log(isMessageDialogVisible, "message ");
    const pollingTimingForConversation = getEnvVariableValue("VITE_MESSAGE_CONVERSATION_TIMING");
    const callAPI = async () => {
      try {
        const res = await ChatServices.checkNewMessageAvailability(params?.stageUid ?? "");
        setIsNewMessageAvailable(res?.data);
      } catch (error) {
        console.log(error);
      }
    };
    callAPI();
    const intervalId = setInterval(callAPI, pollingTimingForConversation);
    return () => {
      clearInterval(intervalId);
    };
  }, []);

  useEffect(() => {
    if (params?.stageUid) {
      const stageDetail = JSON.parse(getLocalStorage(LOCAL_STORAGE.STAGE_DETAIL));
      if (
        stageDetail?.status === LOAN_STAGE_STATUS.COMPLETED ||
        stageDetail?.status === LOAN_STAGE_STATUS.CANCELLED
      ) {
        setButtonDisabled((prevState) => ({ ...prevState, stageComplete: true, submit: true }));
      } else if (stageDetail?.status === LOAN_STAGE_STATUS.IN_REVIEW) {
        setButtonDisabled((prevState) => ({ ...prevState, stageComplete: false, submit: true }));
      } else {
        setButtonDisabled((prevState) => ({ ...prevState, stageComplete: false, submit: false }));
      }
    }
  }, [params?.stageUid]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  const getLoanDetail = () => {
    const uid = params.loanUid;
    const search = `&uid=${uid}`;
    LoanService.getAll(0, 1, search)
      .then((res) => {
        const results = res.data.results[0];
        dispatch(updateLoanDetail(results));
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const onStageComplete = async () => {
    try {
      const res = await TechnicalService.stageComplete(params?.stageUid ?? "", {});

      if (validatePutAPIResponse(res)) {
        setToastMessage("Stage completed successfully");
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setTriggerToast(!triggerToast);
        if (ACLService.isRouteHasAccess(LINK_DDE_STAGE_AND_STATUS)) {
          const URL = `${linkBasePath}${linkAuthRoute}${LINK_LOAN_DETAIL}${LINK_DDE_STAGE_AND_STATUS}/${params.loanUid}/`;
          window.location.href = URL;
        }
      }
    } catch (error) {
      console.log(error);
      setShowDialogAPIError(true);
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onSubmit = async () => {
    try {
      const res = await TechnicalService.submit(params?.stageUid ?? "", {});

      if (validatePutAPIResponse(res)) {
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setTriggerToast(!triggerToast);
      }
    } catch (error) {
      console.log(error);
      setShowDialogAPIError(true);
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const getClassMenu = ({ isActive }: any) => {
    const className = `tab text-sm px-2.5 py-1 min-w-fit ${
      isActive
        ? "bg-secondary-steelBlue-900 rounded-sm text-white font-medium shadow-activeBtn "
        : "text-primary-500"
    }`;
    return className;
  };
  const [dataForViewDetails, setDataForViewDetails] = useState({
    assignedTo: "",
    delegateTo: "",
    initiation: "",
    internalVendor: "",
    externalVendor: "",
    status: "",
  });
  const overlayPanelRef = useRef(null);
  const onViewDetails = (e: any) => {
    const viewDetailData: any = localStorage.getItem("stageDetail");
    setDataForViewDetails({
      assignedTo: JSON.parse(viewDetailData)?.assignee?.name ?? "NA",
      delegateTo: JSON.parse(viewDetailData)?.delegate?.name ?? "NA",
      initiation: JSON.parse(viewDetailData)?.createdAtSystemDate ?? "NA",
      externalVendor: JSON.parse(viewDetailData)?.partner.name ?? "NA",
      internalVendor: JSON.parse(viewDetailData)?.internalVendor ?? "NA",
      status: JSON.parse(viewDetailData)?.status ?? "NA",
    });
    if (overlayPanelRef.current) {
      // @ts-ignore: Will fix later
      overlayPanelRef.current?.toggle(e);
    }
  };
  const onToggleMessage = (e: any) => {
    setIsMessageDialogVisible(!isMessageDialogVisible);
  };
  const renderLoanDetail = () => {
    return (
      <div className="flex flex-col gap-4 p-5 ">
        <div className="flex flex-row items-center justify-between">
          <div className="text-sm text-secondary-stext-900">Status :</div>
          <div className="ml-2 text-sm font-bold text-secondary-stext-900">
            {dataForViewDetails?.status}
          </div>
        </div>
        <div className="flex flex-row items-center justify-between">
          <div className="text-sm text-secondary-stext-900">Assigned to :</div>
          <div className="ml-2 text-sm font-bold text-secondary-stext-900">
            {dataForViewDetails?.assignedTo}
          </div>
        </div>
        <div className="flex flex-row items-center justify-between">
          <div className="text-sm text-secondary-stext-900">Delegated to :</div>
          <div className="ml-2 text-sm font-bold text-secondary-stext-900">
            {dataForViewDetails?.delegateTo}
          </div>
        </div>
        <div className="flex flex-row items-center justify-between">
          <div className="text-sm text-secondary-stext-900">Date of Initiation :</div>
          <div className="ml-2 text-sm font-bold text-secondary-stext-900">
            {formatDate(dataForViewDetails?.initiation)}
          </div>
        </div>
        {dataForViewDetails?.internalVendor !== "NA" ? (
          <div className="flex flex-row items-center justify-between">
            <div className="text-sm text-secondary-stext-900">Internal Vendor :</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {dataForViewDetails.internalVendor !== "NA"
                ? dataForViewDetails.internalVendor
                : "NA"}
            </div>
          </div>
        ) : null}

        {dataForViewDetails?.externalVendor !== "NA" ? (
          <div className="flex flex-row items-center justify-between">
            <div className="text-sm text-secondary-stext-900">External Vendor :</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {dataForViewDetails.externalVendor !== "NA"
                ? dataForViewDetails.externalVendor
                : "NA"}
            </div>
          </div>
        ) : null}
      </div>
    );
  };
  return (
    <div className="flex flex-col gap-5 ">
      <div className="flex items-center justify-between w-full gap-5">
        <div className="flex items-center w-11/12 gap-5">
          <div className="flex items-center gap-2 px-5 py-2 border-r border-secondary-stext-600 ">
            <Text
              className="font-bold text-primary-ptext-1000"
              label={"heading.technical.technicalReport"}
            ></Text>
            <TextButton
              className="underline text-xs  !text-black  "
              label={"View Details"}
              onClick={onViewDetails}
            />
            <button onClick={onToggleMessage} className="relative ">
              <img src={chatIcon} alt="ChatIcon" className="" />{" "}
              {isNewMessageAvailable ? (
                <div className="absolute top-0 right-0 w-2 h-2 bg-red-500 rounded-full"></div>
              ) : (
                <></>
              )}
            </button>
          </div>

          <div className="w-3/5">
            {TECHNICAL_MENU.map((menu) => (
              <NavLink
                key={menu.path}
                className={getClassMenu}
                to={`${linkAuthRoute}${LINK_LOAN_DETAIL}${LINK_TECHNICAL}/${params.loanUid}/${params.stageUid}${menu.path}`}
              >
                {menu.name}
              </NavLink>
            ))}
          </div>
        </div>

        <div className="flex justify-end w-1/4 gap-4">
          {!isPartnerUser ? (
            <ButtonBox
              buttonType={BUTTON_TYPE.STAGE_COMPLETE}
              onClickWithLoader={onStageComplete}
              size="LOAN_DATA"
              disabled={buttonDisabled.stageComplete}
            />
          ) : (
            <></>
          )}

          <ButtonBox
            buttonType={BUTTON_TYPE.SUBMIT}
            onClickWithLoader={onSubmit}
            size="LOAN_DATA"
            disabled={buttonDisabled.submit}
          />
        </div>
      </div>

      <div className="technical-layout-body-content">
        <AclGuard override={{ read: buttonDisabled.stageComplete }}>
          <Suspense fallback={<Loader />}>
            <Routes>
              {technicalRoutes.map((route, index) => {
                return (
                  <Route
                    key={index}
                    path={route.path}
                    element={
                      <AuthValidator
                        AuthorizedComponent={route.component}
                        urlPath={route.altPath ? route.altPath : route.path}
                      />
                    }
                  />
                );
              })}
            </Routes>
          </Suspense>
          <Outlet />
        </AclGuard>
      </div>
      {isMessageDialogVisible ? (
        <DialogBox
          visible={isMessageDialogVisible}
          closable={false}
          model={"false"}
          onHide={() => setIsMessageDialogVisible(false)}
          className="rounded-lg shadow-lg !w-240 bg-blend-darken !p-0 !h-162"
        >
          <MainMessageBody params={params} handleCloseOverLay={onToggleMessage} />
        </DialogBox>
      ) : null}
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
      <SimpleOverlayPanel
        className="rounded-lg shadow-lg !w-80 p-1.5 "
        ref={overlayPanelRef}
        content={renderLoanDetail()}
      />
      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
        onRemove={() => {
          const URL = linkBasePath + linkAuthRoute + LINK_PARTNER_TASK_LIST;
          window.location.href = URL;
        }}
      />
    </div>
  );
};

export default PageTechnical;
